import React, {memo, useCallback} from 'react';
import {ScreenWrapper, UIText} from '@true/ui';
import {CustomHeader} from '@/components/CustomHeader';
import {Fonts, styled} from '@true/core';
import {NativeScrollEvent, NativeSyntheticEvent, View} from 'react-native';
import {typeUpdates} from '@/screens/UpdateTypesScreen/typeUpdates';
import {FlashList} from '@shopify/flash-list';
import Animated, {
  Extrapolation,
  interpolate,
  SharedValue,
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import {IC_FILLED_BACKWARD} from '@true/asset';

const ItemType = memo(function ItemType({
  item,
  offset,
  expandedEnd,
}: {
  item: string;
  offset: SharedValue<number>;
  expandedEnd: SharedValue<boolean>;
}) {
  const animatedSubTitle = useAnimatedStyle(() => {
    const active = offset.value > 10 ? 1 : 0;
    return {
      maxHeight: withTiming(
        interpolate(active, [0, 1], [0, 32], Extrapolation.CLAMP),
        {},
        finished => {
          if (finished) {
            expandedEnd.value = true;
          }
        },
      ),
      overflow: 'scroll',
    };
  });
  return (
    <SWrapperBox onPress={typeUpdates[item].onPress}>
      <View
        style={{
          flexWrap: 'wrap',
          flexDirection: 'row',
        }}>
        <SIconBox>
          <SIconImage source={typeUpdates[item].icon} />
        </SIconBox>
        <View
          style={{
            maxWidth: 220,
            marginLeft: 16,
            justifyContent: 'center',
          }}>
          <STitle>{typeUpdates[item].title}</STitle>
          <Animated.View style={animatedSubTitle}>
            <SubTitle>{typeUpdates[item].subTitle}</SubTitle>
          </Animated.View>
        </View>
      </View>
      <SArrowIcon>
        <SImage source={IC_FILLED_BACKWARD} />
      </SArrowIcon>
    </SWrapperBox>
  );
});

export const UpdateTypesScreen = memo(function UpdateTypesScreen() {
  const offset = useSharedValue(0);
  const expandedEnd = useSharedValue(false);
  const renderItem = useCallback(({item, index}: any) => {
    return <ItemType item={item} offset={offset} expandedEnd={expandedEnd} />;
  }, []);
  const onScroll = useWorkletCallback(
    (event: NativeSyntheticEvent<NativeScrollEvent>) => {
      offset.value = event.nativeEvent.contentOffset.y;
    },
    [],
  );

  const animatedFooterStyle = useAnimatedStyle(() => ({
    width: '100%',
    height: expandedEnd.value ? 0 : 1000,
  }));

  return (
    <ScreenWrapper>
      <CustomHeader title={'Types'} isSearch={false} isDivider />
      <SListView>
        <FlashList
          renderItem={renderItem}
          onScroll={onScroll}
          data={[
            'events',
            'announcement',
            'update',
            'idea',
            'topics',
            'polls',
            'welcome',
            'companyletters',
            'companycorevalues',
            'external',
          ]}
          showsVerticalScrollIndicator={false}
          ListFooterComponent={<Animated.View style={animatedFooterStyle} />}
        />
      </SListView>
    </ScreenWrapper>
  );
});
const SWrapperBox = styled.TouchableOpacity`
  display: flex;
  padding: 12px 16px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
  border-radius: 8px;
  border: 1px solid ${p => p.theme.neutral5};
`;
const SIconBox = styled.TouchableOpacity`
  width: 32px;
  height: 32px;
  align-items: center;
  justify-content: center;
  background-color: #f3f3fc;
  border-radius: 4px;
`;
const SImage = styled.Image`
  width: 20px;
  height: 20px;
  tint-color: ${p => p.theme.neutral3};
`;
const STitle = styled(UIText.BodyMedium500)`
  color: ${p => p.theme.neutral1};
`;
const SubTitle = styled(UIText.CaptionMedium400)`
  color: ${p => p.theme.neutral2};
`;
const SArrowIcon = styled(SIconBox)`
  background-color: white;
  transform: rotate(180deg);
`;
const SIconImage = styled(SImage)`
  tint-color: ${p => p.theme.primaryColor};
`;
const SListView = styled.View`
  flex: 1;
  padding: 0 16px;
  margin-top: 16px;
`;
